<script>
import moment from "moment"
import { PROTOCOL_COLORS } from "@/utils/util"
export default {
	name: "asset",
	data() {
		const protocolList = [
			{ text: "RDP", value: "rdp" },
			{ text: "SSH", value: "ssh" },
			{ text: "VNC", value: "vnc" },
			{ text: "Telnet", value: "telnet" },
			{ text: "Kubernetes", value: "kubernetes" },
		]
		const columns = [
			{
				title: "",
				dataIndex: "id",
				key: "id",
				width: 80,
				customRender(text, record, index) {
					return index + 1
				},
			},
			{
				title: "",
				dataIndex: "name",
				key: "name",
				customRender: (name) => {
					let short = name
					if (short && short.length > 20) {
						short = short.substring(0, 20) + " ..."
					}
					return (
						<aTooltip placement="topLeft" title={name}>
							{short}
						</aTooltip>
					)
				},
				sorter: true,
			},
			{
				title: "",
				dataIndex: "protocol",
				key: "protocol",
				width: 120,
				align: "center",
				customRender: (protocol, record) => {
					const title = `${record["ip"] + ":" + record["port"]}`
					return (
						<a-tooltip title={title}>
							<a-tag color={PROTOCOL_COLORS[protocol]}>{protocol}</a-tag>
						</a-tooltip>
					)
				},
				filterMultiple: false,
				filters: protocolList,
			},
			{
				title: "",
				dataIndex: "tags",
				key: "tags",
				align: "center",
				customRender: (tags) => {
					let tagDocuments = []
					let tagArr = tags.split(",")
					for (let i = 0; i < tagArr.length; i++) {
						if (tags[i] === "-") {
							continue
						}
						tagDocuments.push(<a-tag key={tagArr[i]}>{tagArr[i]}</a-tag>)
					}
					return tagDocuments
				},
				scopedSlots: { customRender: "tags" },
			},
			{
				title: "",
				dataIndex: "active",
				key: "active",
				align: "center",
				width: 100,
				customRender: (text) => {
					if (text) {
						return (
							<aTooltip title="">
								<aBadge status="processing" />
							</aTooltip>
						)
					} else {
						return (
							<aTooltip title="">
								<aBadge status="error" />
							</aTooltip>
						)
					}
				},
			},
			{
				title: "",
				dataIndex: "ownerName",
				key: "ownerName",
				align: "center",
			},
			{
				title: "",
				dataIndex: "sharerCount",
				key: "sharerCount",
				width: 100,
				align: "center",
				scopedSlots: {
					customRender: "sharerCount",
				},
			},
			{
				title: "",
				dataIndex: "created",
				key: "created",
				width: 150,
				align: "center",
				customRender: (created) => {
					return <a-tooltip title={created}>{moment(created).fromNow()}</a-tooltip>
				},
				sorter: true,
			},
			{
				title: "",
				key: "action",
				align: "right",
				width: 360,
				scopedSlots: {
					customRender: "action",
				},
			},
		]
		return {
			PROTOCOL_COLORS,
			columns,
			items: [],
			total: 0,
			queryParams: {
				name: "",
				ip: "",
				tags: [],
				pageIndex: 1,
				pageSize: 10,
				protocol: "",
			},
			asset: {
				protocol: "ssh",
				accountType: "custom",
				port: "",
				name: "",
				ip: "",
				"ssh-mode": "",
				"color-scheme": "",
				backspace: "",
				"terminal-type": "",
				username: "",
				password: "",
				tags: [],
				description: "",
				"color-depth": "",
				cursor: "",
				"dest-host": "",
				"remote-app": "",
				"remote-app-dir": "",
				"remote-app-args": "",
				"font-name": "",
				"font-size": "",
				"username-regex": "",
				"password-regex": "",
				"login-failure-regex": "",
				"login-success-regex": "",
				"use-ssl": false,
				"client-cert": "",
				"client-key": "",
				"ca-cert": "",
				"ignore-cert": false,
				namespace: "123",
				container: "1231",
			},
			assetRules: {
				name: [{ required: true, message: "" }],
				ip: [{ required: true, message: "IP" }],
				protocol: [{ required: true, message: "" }],
				port: [{ required: true, message: "" }],
				pod: [{ required: true, message: "Pod" }],
				accountType: [{ required: true, message: "" }],
				credentialId: [{ required: true, message: "" }],
				privateKey: [{ required: true, message: "" }],
			},
			loading: false,
			modalVisible: false,
			modalTitle: "",
			modalConfirmLoading: false,
			credentials: [],
			tags: [],
			protocolList,
			model: {},
			selectedRowKeys: [],
			delBtnLoading: false,
			changeOwnerModalVisible: false,
			changeSharerModalVisible: false,
			changeOwnerConfirmLoading: false,
			changeSharerConfirmLoading: false,
			users: [],
			selected: {},
			selectedSharers: [],
		}
	},
	methods: {
		getTags() {
			this.$http.get("/tags").then((res) => {
				this.tags = res.data
			})
		},
		loadTableData(queryParams, pageIndex) {
			if (queryParams) {
				this.queryParams = Object.assign(this.queryParams, queryParams, { pageIndex: pageIndex || this.queryParams.pageIndex })
			}
			this.loading = true
			this.$http
				.get("/assets/paging", { params: { ...this.queryParams, tags: this.queryParams.tags.length ? this.queryParams.tags.join(",") : undefined } })
				.then((res) => {
					this.items = res.data.items
					this.total = res.data.total
				})
				.finally(() => {
					this.loading = false
				})
		},
		onSelectChange(selectedRowKeys) {
			this.selectedRowKeys = selectedRowKeys
		},
		deletePost(ids) {
			if (typeof ids === "string") {
				ids = [ids]
			}
			if (!ids.length) {
				return false
			}
			this.delBtnLoading = true
			this.$http.delete("/assets/" + ids.join(",")).then(() => {
				this.$message.success("")
				this.delBtnLoading = false
				this.loadTableData()
			})
		},
		handleTableChange(pagination, filters, sorter) {
			for (let i in filters) {
				filters[i] = filters[i][0] || undefined
			}
			let query = {
				...this.queryParams,
				...filters,
				pageIndex: pagination.current,
				pageSize: pagination.pageSize,
				order: sorter.order,
				field: sorter.field,
			}

			this.loadTableData(query)
		},
		handleProtocolChange() {
			this.asset.accountType = "custom"
			switch (this.asset.protocol) {
				case "rdp":
					this.asset.port = 3389
					break
				case "ssh":
					this.asset.port = 22
					break
				case "vnc":
					this.asset.port = 5900
					break
				case "telnet":
					this.asset.port = 23
					break
				case "kubernetes":
					this.asset.port = 6443
					break
			}
		},
	},
	created() {
		this.loadTableData()
	},
}
</script>

